/* ----------------------------------------------------------------
    10. Header
-----------------------------------------------------------------*/

#wp-admin-bar-anva_node_optons {
    > .ab-item:before {
        content: "\f540";
        top: 2px;
    }
}

#header {
    border-bottom: 1px solid #F5F5F5;
    position: relative;
    background-color: #FFF;
}

#header .container {
    position: relative;
}

#header.transparent-header {
    background: transparent;
    border-bottom: none;
    z-index: 199;
}

#header.semi-transparent {
    background-color: rgba(255, 255, 255, 0.8);
}

#header.floating-header.semi-transparent {
    background-color: transparent;
}

#header.transparent-header.floating-header {
    margin-top: $header-floating-offset;
}

#header.transparent-header.floating-header .container {
    width: 1220px;
    background-color: #FFF;
    padding: 0 40px;
    border-radius: 2px;
}

#header.floating-header.transparent-header.semi-transparent .container {
    background-color: rgba(255, 255, 255, 0.8);
}

#header.floating-header.transparent-header.semi-transparent.sticky-header .container {
    background-color: transparent;
}

#header.transparent-header.floating-header.sticky-header .container {
    width: 1170px;
    padding: 0 15px;
}

#header.transparent-header + #slider,
#header.transparent-header + #page-title.page-title-parallax,
#header.transparent-header + #google-map,
#slider + #header.transparent-header {
    top: (-$header-height);
    margin-bottom: (-$header-height);
}

#header.transparent-header.floating-header + #slider,
#header.transparent-header.floating-header + #google-map {
    top: -($header-height + $header-floating-offset);
    margin-bottom: -($header-height + $header-floating-offset);
}

#header.transparent-header + #page-title.page-title-parallax .container {
    z-index: 5;
    padding-top: $header-height;
}

#header.full-header {
    border-bottom-color: #EEE;
}

body.stretched #header.full-header .container {
    width: 100%;
    padding: 0 30px;
}

#header.transparent-header.full-header #header-wrap {
    border-bottom: 1px solid rgba(black, 0.1);
}

#slider + #header.transparent-header.full-header #header-wrap {
    border-top: 1px solid rgba(black, 0.1);
    border-bottom: none;
}


/* ----------------------------------------------------------------
    Logo
-----------------------------------------------------------------*/

#logo {
    position: relative;
    float: left;
    font-family: $heading-font;
    font-size: 36px;
    line-height: 100%;
    margin-right: 40px;
}

// Logo with Text
#logo > div > a {
    display: block;
    line-height: 22px;
    padding: 39px 15px;
    color: #444;
    font-weight: 700;
    font-size: 20px;
    text-transform: uppercase;
}

// Logo with Tagline
#logo.logo-has-tagline {
    > div > a {
        padding: 28px 15px 0;
    }
    span.logo-tagline {
        line-height: 22px;
        font-size: 13px;
        display: block;
        padding: 0 15px 28px;
    }
}

#header.sticky-header {
    #logo.logo-has-tagline {
        > div > a {
            padding: 8px 15px 0;
        }
        span.logo-tagline {
            padding: 0 15px 8px;
        }
    }
}

#header.full-header #logo {
    padding-right: 30px;
    margin-right: 30px;
    border-right: 1px solid #EEE;
}

#header.transparent-header.full-header #logo {
    border-right-color: rgba(black, 0.1);
}

#logo a {
    display: block;
    color: #000;
}

#logo img {
    display: block;
    max-width: 100%;
}

#logo a.standard-logo {
    display: block;
}

#logo a.retina-logo {
    display: none;
}


/* ----------------------------------------------------------------
    Header Right Area
-----------------------------------------------------------------*/

.header-extras {
    float: right;
    margin: ( ($header-height - 40px) / 2) 0 0;
}

.header-extras li {
    float: left;
    margin-left: 20px;
    height: 40px;
    overflow: hidden;
    list-style: none;
}

.header-extras li i {
    margin-top: 3px !important;
}

.header-extras li:first-child {
    margin-left: 0;
}

.header-extras li .he-text {
    float: left;
    padding-left: 10px;
    font-weight: bold;
    font-size: 14px;
    line-height: 1.43;
}

.header-extras li .he-text span {
    display: block;
    font-weight: 400;
    color: $theme-color;
}


/* ----------------------------------------------------------------
    Primary Menu
-----------------------------------------------------------------*/

#primary-menu {
    float: right;
}

#primary-menu ul.mobile-primary-menu {
    display: none;
}

#header.full-header #primary-menu > ul,
#header.full-header #primary-menu > div.menu-message {
    float: left;
    padding-right: 15px;
    margin-right: 15px;
    border-right: 1px solid #EEE;
}

#header.transparent-header.full-header #primary-menu > ul,
#header.transparent-header.full-header #primary-menu > div.menu-message {
    border-right-color: rgba(black, 0.1);
}

#header.full-header #primary-menu.style-2 > ul {
    border-right: 0;
}

#primary-menu-trigger,
#page-submenu-trigger {
    opacity: 0;
    pointer-events: none;
    cursor: pointer;
    font-size: 14px;
    position: absolute;
    top: 50%;
    margin-top: -25px;
    width: 50px;
    height: 50px;
    line-height: 50px;
    text-align: center;
    transition: opacity 0.3s ease;
}

#primary-menu ul {
    list-style: none;
    margin: 0;
}

#primary-menu > ul,
#primary-menu > div.menu-message {
    float: left;
}

#primary-menu > div.menu-message {
    margin: 0;
    line-height: 99px;
}

#header.sticky-header #primary-menu > div.menu-message {
    line-height: 59px;
    transition: all 0.4s ease;
}

#primary-menu ul li {
    position: relative;
}

#primary-menu ul li.mega-menu {
    position: inherit;
}

#primary-menu ul > li {
    float: left;
    margin-left: 2px;
}

#primary-menu > ul > li:first-child {
    margin-left: 0;
}

#primary-menu ul li > a,
#primary-menu ul li > span a {
    display: block;
    line-height: 22px;
    padding: $primary-menu-padding 15px;
    color: $primary-menu-font-color;
    font-weight: $primary-menu-font-weight;
    font-size: $primary-menu-font-size;
    letter-spacing: $primary-menu-font-spacing;
    text-transform: $primary-menu-font-tt;
    font-family: $heading-font;
    transition: margin 0.4s ease, padding 0.4s ease;
}

#primary-menu ul li > a span {
    display: none;
}

#primary-menu ul li > a i {
    position: relative;
    top: -1px;
    font-size: 14px;
    width: 16px;
    text-align: center;
    margin-right: 6px;
    vertical-align: top;
}

#primary-menu ul li > a i.icon-angle-down:last-child {
    font-size: 12px;
    margin: 0 0 0 5px;
}

#primary-menu ul li:hover > a,
#primary-menu ul li.current > a {
    color: $theme-color;
}

body.no-superfish #primary-menu li:hover > ul:not(.mega-menu-column),
body.no-superfish #primary-menu li:hover > .mega-menu-content {
    display: block;
}

#primary-menu ul ul:not(.mega-menu-column),
#primary-menu ul li .mega-menu-content {
    display: none;
    position: absolute;
    width: $primary-menu-submenu-width;
    background-color: #FFF;
    box-shadow: 0 13px 42px 11px rgba(black, 0.05);
    border: 1px solid #EEE;
    border-top: 2px solid $theme-color;
    height: auto;
    z-index: 199;
    top: 100%;
    left: 0;
    margin: 0;
}

#primary-menu ul ul:not(.mega-menu-column) ul {
    top: -2px !important;
    left: $primary-menu-submenu-width - 2;
}

#primary-menu ul ul.menu-pos-invert:not(.mega-menu-column),
#primary-menu ul li .mega-menu-content.menu-pos-invert {
    left: auto;
    right: 0;
}

#primary-menu ul ul:not(.mega-menu-column) ul.menu-pos-invert {
    right: $primary-menu-submenu-width - 2;
}

#primary-menu ul ul li {
    float: none;
    margin: 0;
}

#primary-menu ul ul li:first-child {
    border-top: 0;
}

#primary-menu ul ul li > a {
    font-size: $primary-menu-submenu-font-size;
    font-weight: $primary-menu-submenu-font-weight;
    color: #666;
    padding-top: 10px;
    padding-bottom: 10px;
    border: 0;
    letter-spacing: 0;
    font-family: $primary-menu-submenu-font;
    transition: all 0.2s ease-in-out;
}

#primary-menu ul ul li > a i {
    top: 0;
}

#primary-menu ul ul li:hover > a {
    background-color: #F9F9F9;
    padding-left: 18px;
    color: $theme-color;
}

#primary-menu ul ul > li.sub-menu > a,
#primary-menu ul ul > li.sub-menu:hover > a,
#primary-menu ul ul > li.sub-menu > span a,
#primary-menu ul ul > li.sub-menu:hover > span a {
    background-image: url("../images/icons/submenu.png");
    background-position: right center;
    background-repeat: no-repeat;
}


/* ----------------------------------------------------------------
    Mega Menu
-----------------------------------------------------------------*/

#primary-menu ul li .mega-menu-content,
#primary-menu ul li.mega-menu-small .mega-menu-content {
    width: 720px;
    max-width: 400px;
}

#primary-menu ul li.mega-menu .mega-menu-content,
.floating-header.sticky-header #primary-menu ul li.mega-menu .mega-menu-content {
    margin: 0 15px;
    width: 1140px;
    max-width: none;
}

.floating-header #primary-menu ul li.mega-menu .mega-menu-content {
    margin: 0;
    width: 1220px;
}

body.stretched .container-fullwidth #primary-menu ul li.mega-menu .mega-menu-content {
    margin: 0 60px;
}

body.stretched #header.full-header #primary-menu ul li.mega-menu .mega-menu-content {
    margin: 0 30px;
}

#primary-menu ul li.mega-menu .mega-menu-content.style-2 {
    padding: 0 10px;
}

#primary-menu ul li .mega-menu-content ul {
    display: block;
    position: relative;
    top: 0;
    min-width: inherit;
    border: 0;
    box-shadow: none;
    background-color: transparent;
}

#primary-menu ul li .mega-menu-content ul:not(.megamenu-dropdown) {
    display: block !important;
    opacity: 1 !important;
    top: 0;
    left: 0;
}

#primary-menu ul li .mega-menu-content div.mega-menu-column {
    float: left;
    margin: 0;
}

#primary-menu ul li .mega-menu-content div.mega-menu-column.col-5 {
    width: 20%;
}

#primary-menu ul li .mega-menu-content ul:not(.mega-menu-column) {
    width: 100%;
}

#primary-menu ul li .mega-menu-content div.mega-menu-column:not(:first-child) {
    border-left: 1px solid #F2F2F2;
}

#primary-menu ul li .mega-menu-content.style-2 div.mega-menu-column {
    padding: 30px 20px;
}

#primary-menu ul li .mega-menu-content.style-2 div.mega-menu-column > ul > li.mega-menu-title {
    margin-top: 20px;
}

#primary-menu ul li .mega-menu-content.style-2 div.mega-menu-column > ul > li.mega-menu-title:first-child {
    margin-top: 0;
}

#primary-menu ul li .mega-menu-content.style-2 div.mega-menu-column > ul > li.mega-menu-title > span {
    display: block;
}

#primary-menu ul li .mega-menu-content.style-2 div.mega-menu-column > ul > li.mega-menu-title > span a {
    font-size: 13px;
    font-weight: bold;
    font-family: $mega-menu-title;
    letter-spacing: 1px;
    text-transform: uppercase !important;
    margin-bottom: 15px;
    color: #444;
    padding: 0 !important;
    line-height: 1.3 !important;
}

#primary-menu ul li .mega-menu-content.style-2 div.mega-menu-column > ul > li.mega-menu-title:hover > span a {
    background-color: transparent;
}

#primary-menu ul li .mega-menu-content.style-2 div.mega-menu-column > ul > li.mega-menu-title > span a:hover {
    color: $theme-color;
}

#primary-menu ul li .mega-menu-content.style-2 div.mega-menu-column > ul > li.mega-menu-title.sub-menu > span a,
#primary-menu ul li .mega-menu-content.style-2 div.mega-menu-column > ul > li.mega-menu-title.sub-menu:hover > span a {
    background: none;
}

#primary-menu ul li .mega-menu-content.style-2 li {
    border: 0;
}

#primary-menu ul li .mega-menu-content.style-2 ul li > a {
    padding-left: 5px;
    padding-top: 7px;
    padding-bottom: 7px;
}

#primary-menu ul li .mega-menu-content.style-2 ul li > a:hover {
    padding-left: 12px;
}

.mega-menu-column .entry-meta {
    margin: 8px -10px 0 0 !important;
    border: none !important;
    padding: 0 !important;
}

.mega-menu-column .entry-meta li {
    float: left !important;
    border: none !important;
    margin: 0 10px 0 0 !important;
}


/* Primary Menu - Style 2
-----------------------------------------------------------------*/

#primary-menu.style-2 {
    float: none;
    max-width: none;
    border-top: 1px solid #F5F5F5;
}

#primary-menu.style-2 > div > ul {
    float: left;
}

#primary-menu.style-2 > div > ul > li > a {
    padding-top: 19px;
    padding-bottom: 19px;
}

#logo + #primary-menu.style-2 {
    border-top: 0;
}

#primary-menu.style-2 > div #top-search,
#primary-menu.style-2 > div #top-lang,
#primary-menu.style-2 > div #top-cart,
#primary-menu.style-2 > div #side-panel-trigger {
    margin-top: 20px;
    margin-bottom: 20px;
}

body:not(.device-sm):not(.device-xs):not(.device-xxs) #primary-menu.style-2.center > ul,
body:not(.device-sm):not(.device-xs):not(.device-xxs) #primary-menu.style-2.center > div {
    float: none;
    display: inline-block !important;
    width: auto;
    text-align: left;
}


/* Primary Menu - Style 3
-----------------------------------------------------------------*/

#primary-menu.style-3 > ul > li > a {
    margin: 28px 0;
    padding-top: 11px;
    padding-bottom: 11px;
    border-radius: 2px;
}

#primary-menu.style-3 > ul > li:hover > a {
    color: #444;
    background-color: #F5F5F5;
}

#primary-menu.style-3 > ul > li.current > a {
    color: #FFF;
    text-shadow: 1px 1px 1px rgba(black, 0.2);
    background-color: $theme-color;
}


/* Primary Menu - Style 4
-----------------------------------------------------------------*/

#primary-menu.style-4 > ul > li > a {
    margin: 28px 0;
    padding-top: 10px;
    padding-bottom: 10px;
    border-radius: 2px;
    border: 1px solid transparent;
}

#primary-menu.style-4 > ul > li:hover > a,
#primary-menu.style-4 > ul > li.current > a {
    border-color: $theme-color;
}


/* Primary Menu - Style 5
-----------------------------------------------------------------*/

#primary-menu.style-5 > ul {
    padding-right: 10px;
    margin-right: 5px;
    border-right: 1px solid #EEE;
}

#primary-menu.style-5 > ul > li:not(:first-child) {
    margin-left: 15px;
}

#primary-menu.style-5 > ul > li > a {
    padding-top: 25px;
    padding-bottom: 25px;
    line-height: 14px;
}

#primary-menu.style-5 > ul > li > a i {
    display: block;
    width: auto;
    margin: 0 0 8px;
    font-size: 28px;
    line-height: 1;
}


/* Primary Menu - Style 6
-----------------------------------------------------------------*/

#primary-menu.style-6 > ul > li > a {
    position: relative;
}

#primary-menu.style-6 > ul > li > a:after,
#primary-menu.style-6 > ul > li.current > a:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    height: 2px;
    width: 0;
    border-top: 2px solid $theme-color;
    transition: width 0.3s ease;
}

#primary-menu.style-6 > ul > li.current > a:after,
#primary-menu.style-6 > ul > li:hover > a:after {
    width: 100%;
}


/* Primary Menu - Sub Title
-----------------------------------------------------------------*/

#primary-menu.sub-title > ul > li,
#primary-menu.sub-title.style-2 > div > ul > li {
    background: url("../images/icons/menu-divider.png") no-repeat right center;
    margin-left: 1px;
}

#primary-menu.sub-title ul li:first-child {
    padding-left: 0;
    margin-left: 0;
}

#primary-menu.sub-title > ul > li > a,
#primary-menu.sub-title > div > ul > li > a {
    line-height: 14px;
    padding: 27px 20px 32px;
    text-transform: uppercase;
    border-top: 5px solid transparent;
}

#primary-menu.sub-title > ul > li > a span,
#primary-menu.sub-title > div > ul > li > a span {
    display: block;
    margin-top: 10px;
    line-height: 12px;
    font-size: 11px;
    font-weight: 400;
    color: #888;
    text-transform: capitalize;
}

#primary-menu.sub-title > ul > li:hover > a,
#primary-menu.sub-title > ul > li.current > a,
#primary-menu.sub-title > div > ul > li:hover > a,
#primary-menu.sub-title > div > ul > li.current > a {
    background-color: $theme-color;
    color: #FFF;
    text-shadow: 1px 1px 1px rgba(black, 0.2);
    border-top-color: rgba(black, 0.1);
}

#primary-menu.sub-title > ul > li:hover > a span,
#primary-menu.sub-title > ul > li.current > a span,
#primary-menu.sub-title.style-2 > div > ul > li:hover > a span,
#primary-menu.sub-title.style-2 > div > ul > li.current > a span {
    color: #EEE;
}

#primary-menu.sub-title.style-2 ul ul span {
    display: none;
}


/* Primary Menu - Style 2 with Sub Title
-----------------------------------------------------------------*/

#primary-menu.sub-title.style-2 > div > ul > li {
    background-position: left center;
}

#primary-menu.sub-title.style-2 > div #top-search,
#primary-menu.sub-title.style-2 > div #top-lang,
#primary-menu.sub-title.style-2 > div #top-cart,
#primary-menu.sub-title.style-2 > div #side-panel-trigger {
    float: right;
    margin-top: 30px;
    margin-bottom: 30px;
}

#primary-menu.sub-title.style-2 div ul li:first-child,
#primary-menu.sub-title.style-2 > div > ul > li:hover + li,
#primary-menu.sub-title.style-2 > div > ul > li.current + li {
    background-image: none;
}

#primary-menu.sub-title.style-2 > div > ul > li > a {
    padding-top: 17px;
    padding-bottom: 22px;
}

/* Primary Menu - Split Menu
-----------------------------------------------------------------*/

@media (min-width: 992px) {

    #header.split-menu #logo {
        position: absolute;
        width: 100%;
        text-align: center;
        margin: 0;
        float: none;
        height: 100px;
    }

    #header.split-menu #logo a.standard-logo { display: inline-block; }

}

#header.split-menu #primary-menu {
    float: none;
    margin: 0;
}

#header.split-menu #primary-menu > ul { z-index: 199; }

#header.split-menu #primary-menu > ul:first-child { float: left; }

#header.split-menu #primary-menu > ul:last-child { float: right; }


/* Primary Menu - Overlay Menu
-----------------------------------------------------------------*/

@media (min-width: 992px) {
    .overlay-menu:not(.top-search-open) #primary-menu-trigger {
        opacity: 1;
        pointer-events: auto;
        left: auto;
        right: 100px;
        width: 20px;
        height: 20px;
        line-height: 20px;
        margin-top: -10px;
    }
    .overlay-menu:not(.top-search-open) .full-header #primary-menu-trigger {
        right: 90px;
    }
    .overlay-menu #primary-menu > #overlay-menu-close {
        opacity: 0;
        pointer-events: none;
        position: fixed;
        top: 25px;
        left: auto;
        right: 25px;
        width: 48px;
        height: 48px;
        line-height: 48px;
        font-size: 24px;
        text-align: center;
        color: #444;
        z-index: 300;
        -webkit-transform: translateY(-80px);
        -ms-transform: translateY(-80px);
        -o-transform: translateY(-80px);
        transform: translateY(-80px);
        transition: opacity 0.4s ease, transform 0.45s 0.15s ease;
    }
    .overlay-menu #primary-menu > ul {
        opacity: 0 !important;
        pointer-events: none;
        position: fixed;
        left: 0;
        top: 0;
        width: 100%;
        height: 100% !important;
        z-index: 299;
        background: rgba(255, 255, 255, 0.95);
    }
    .overlay-menu #primary-menu > ul.show {
        opacity: 1 !important;
        display: inherit !important;
        pointer-events: auto;
    }
    .overlay-menu #primary-menu > ul.show ~ #overlay-menu-close {
        opacity: 1;
        transition: opacity 0.7s 0.4s ease, transform 0.45s 0.15s ease;
        transform: translateY(0);
        pointer-events: auto;
    }
    .overlay-menu #primary-menu > ul > li {
        float: none;
        text-align: center;
        max-width: 400px;
        margin: 0 auto;
    }
    .overlay-menu #primary-menu > ul > li > a {
        font-size: 24px;
        padding-top: 19px;
        padding-bottom: 19px;
        letter-spacing: 2px;
        text-transform: none;
        opacity: 0;
        transform: translateY(-80px);
        transition: opacity 0.7s 0.15s ease, transform 0.45s 0.15s ease, color 0.2s linear;
    }
    .overlay-menu #primary-menu > ul.show > li > a {
        opacity: 1;
        transform: translateY(0);
    }
}


/* ----------------------------------------------------------------
    Top Search
-----------------------------------------------------------------*/

#top-search,
#top-lang,
#top-cart,
#side-panel-trigger {
    float: right;
    margin: ( ($header-height - 20) / 2) 0 ( ($header-height - 20) / 2) 15px;
}

#top-lang,
#top-cart {
    position: relative;
}

#top-search a,
#top-lang a,
#top-cart a,
#side-panel-trigger a {
    display: block;
    position: relative;
    width: 14px;
    height: 14px;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    color: #333;
    transition: color 0.3s ease-in-out;
}

#top-search a {
    z-index: 11;
}

#top-search a i {
    position: absolute;
    top: 0;
    left: 0;
    transition: opacity 0.3s ease;
}

body.top-search-open #top-search a i.icon-search3,
#top-search a i.icon-line-cross {
    opacity: 0;
}

body.top-search-open #top-search a i.icon-line-cross {
    opacity: 1;
    z-index: 11;
    font-size: 16px;
}

#top-lang a:hover,
#top-cart a:hover {
    color: $theme-color;
}

#top-search form {
    opacity: 0;
    z-index: -2;
    position: absolute;
    width: 100% !important;
    height: 100% !important;
    padding: 0 15px;
    margin: 0;
    top: 0;
    left: 0;
    transition: opacity 0.3s ease-in-out;
}

body.top-search-open #top-search form {
    opacity: 1;
    z-index: 10;
}

#top-search form input {
    box-shadow: none !important;
    pointer-events: none;
    border-radius: 0;
    border: 0;
    outline: 0 !important;
    font-size: 32px;
    padding: 10px 80px 10px 0;
    height: 100%;
    background-color: transparent;
    color: #333;
    font-weight: 700;
    margin-top: 0 !important;
    font-family: $heading-font;
    letter-spacing: 2px;
}

body:not(.device-sm):not(.device-xs):not(.device-xxs) #header.full-header #top-search form input {
    padding-left: 40px;
}

body:not(.device-sm):not(.device-xs):not(.device-xxs) .container-fullwidth #top-search form input {
    padding-left: 60px;
}

body.top-search-open #top-search form input {
    pointer-events: auto;
}

body:not(.device-sm):not(.device-xs):not(.device-xxs) #header.transparent-header:not(.sticky-header):not(.full-header):not(.floating-header) #top-search form input {
    border-bottom: 2px solid rgba(black, 0.1);
}

#top-search form input::-moz-placeholder {
    color: #555;
    opacity: 1;
    text-transform: uppercase;
}

#top-search form input:-ms-input-placeholder {
    color: #555;
    text-transform: uppercase;
}

#top-search form input::-webkit-input-placeholder {
    color: #555;
    text-transform: uppercase;
}

#primary-menu .container #top-search form input,
.sticky-header #top-search form input {
    border: none !important;
}


/* ----------------------------------------------------------------
    Top Cart
-----------------------------------------------------------------*/

#top-cart {
    margin-right: 0;
}

#top-cart > a > span {
    display: block;
    position: absolute;
    top: -7px;
    left: auto;
    right: -14px;
    font-size: 10px;
    color: #FFF;
    text-shadow: 1px 1px 1px rgba(black, 0.2);
    width: 16px;
    height: 16px;
    line-height: 16px;
    text-align: center;
    background-color: $theme-color;
    border-radius: 50%;
}

#top-cart .top-cart-content {
    opacity: 0;
    z-index: -2;
    position: absolute;
    width: 280px;
    background-color: #FFF;
    box-shadow: 0 20px 50px 10px rgba(black, 0.05);
    border: 1px solid #EEE;
    border-top: 2px solid $theme-color;
    top: ( $header-height - ($header-height - 20) / 2);
    right: -15px;
    left: auto;
    margin: -10000px 0 0;
    transition: opacity 0.5s ease, top 0.4s ease;
}

.style-2 .container #top-lang .top-lang-content,
.style-2 .container #top-cart .top-cart-content {
    top: 40px;
}

.style-2.sub-title .container #top-lang .top-lang-content,
.style-2.sub-title .container #top-cart .top-cart-content {
    top: 50px;
}

#top-cart.top-cart-open .top-cart-content {
    opacity: 1;
    z-index: 11;
    margin-top: 0;
}

.top-cart-title {
    padding: 12px 15px;
    border-bottom: 1px solid #EEE;
}

.top-cart-title h4 {
    margin-bottom: 0;
    font-size: 15px;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 1px;
}

.top-cart-items {
    padding: 15px;
}

.top-cart-item {
    padding-top: 15px;
    margin-top: 15px;
    border-top: 1px solid #F5F5F5;
}

.top-cart-item:first-child {
    padding-top: 0;
    margin-top: 0;
    border-top: 0;
}

.top-cart-item-image {
    float: left;
    margin-right: 15px;
    width: 48px !important;
    height: 48px !important;
    border: 2px solid #EEE;
    transition: border-color 0.2s linear;
}

.top-cart-item-image a,
.top-cart-item-image img {
    display: block;
    width: 44px !important;
    height: 44px !important;
}

.top-cart-item-image:hover {
    border-color: $theme-color;
}

.top-cart-item-desc {
    position: relative;
    overflow: hidden;
}

.top-cart-item-desc a {
    text-align: left !important;
    font-size: 13px !important;
    width: auto !important;
    height: auto !important;
    font-weight: 600;
}

.top-cart-item-desc a:hover {
    color: $theme-color !important;
}

.top-cart-item-desc span.top-cart-item-price {
    display: block;
    line-height: 20px;
    font-size: 12px !important;
    color: #999;
}

.top-cart-item-desc span.top-cart-item-quantity {
    display: block;
    position: absolute;
    font-size: 12px !important;
    right: 0;
    top: 2px;
    color: #444;
}

.top-cart-action {
    padding: 15px;
    border-top: 1px solid #EEE;
}

.top-cart-action span.top-checkout-price {
    font-size: 20px;
    color: $theme-color;
}


/* ----------------------------------------------------------------
    Top Lang WPML
-----------------------------------------------------------------*/

#top-lang .top-lang-content {
    opacity: 0;
    z-index: -2;
    position: absolute;
    width: 180px;
    background-color: #FFF;
    box-shadow: 0 20px 50px 10px rgba(black, 0.05);
    border: 1px solid #EEE;
    border-top: 2px solid $theme-color;
    top: ( $header-height - ($header-height - 20) / 2);
    right: -15px;
    left: auto;
    margin: -10000px 0 0;
    transition: opacity 0.5s ease, top 0.4s ease;
}

#top-lang.top-lang-open .top-lang-content {
    opacity: 1;
    z-index: 11;
    margin-top: 0;
}

.top-lang-content ul.top-lang-switch {
    li {
        display: block;
        float: none !important;
        margin: 3px 0;
        > a {
            font-weight: 400 !important;
            height: auto !important;
            line-height: normal !important;
            padding: 10px !important;
            width: auto !important;
            text-align: left !important;
        }
        &.active {
            background-color: #f9f9f9;
        }
    }
}


/* ----------------------------------------------------------------
    Sticky Header
-----------------------------------------------------------------*/

#header,
#header-wrap,
#logo img {
    height: $header-height;
    transition: height 0.4s ease, opacity 0.3s ease;
}

#header.sticky-style-2 {
    height: ( ($header-height + $sticky-header-height) + 1);
}

#header.sticky-style-3 {
    height: ( ($header-height + $sticky-header-height) + 21);
}

#header.sticky-style-2 #header-wrap {
    height: $sticky-header-height;
}

#header.sticky-style-3 #header-wrap {
    height: $sticky-header-height + 20;
}

#primary-menu > ul > li > ul,
#primary-menu > ul > li > .mega-menu-content,
#primary-menu > ul > li.mega-menu > .mega-menu-content {
    transition: top 0.4s ease;
}

#top-search,
#top-lang,
#top-cart {
    transition: margin 0.4s ease, opacity 0.3s ease;
}

body:not(.top-search-open) #header-wrap #logo,
body:not(.top-search-open) #primary-menu > ul,
body:not(.top-search-open) #primary-menu > div.menu-message,
body:not(.top-search-open) #primary-menu > .container > ul,
body:not(.top-search-open) #top-cart,
body:not(.top-search-open) #side-panel-trigger {
    opacity: 1;
    transition: height 0.4s ease, margin 0.4s ease, opacity 0.2s 0.2s ease;
}

body.top-search-open #header-wrap #logo,
body.top-search-open #primary-menu > ul,
body.top-search-open #primary-menu > div.menu-message,
body.top-search-open #primary-menu > .container > ul,
body.top-search-open #top-cart,
body.top-search-open #top-lang,
body.top-search-open #side-panel-trigger {
    opacity: 0;
}

#header.sticky-header.semi-transparent {
    background-color: transparent;
}

#header-wrap {
    position: relative;
    z-index: 199;
}

#header.sticky-header #header-wrap {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background-color: #FFF;
    box-shadow: 0 0 10px rgba(black, 0.1);
}

body.admin-bar #header.sticky-header #header-wrap {
    top: 32px !important;
}

#header.semi-transparent.sticky-header #header-wrap {
    background-color: rgba(255, 255, 255, 0.8);
}

#header.sticky-header:not(.static-sticky),
#header.sticky-header:not(.static-sticky) #header-wrap,
#header.sticky-header:not(.static-sticky):not(.sticky-style-2):not(.sticky-style-3) #logo img {
    height: $sticky-header-height;
}

#header.sticky-header:not(.static-sticky) #primary-menu > ul > li > a {
    padding-top: $sticky-menu-padding;
    padding-bottom: $sticky-menu-padding;
}

#header.sticky-header:not(.static-sticky).sticky-style-2,
#header.sticky-header:not(.static-sticky).sticky-style-3 {
    height: (($header-height + $sticky-header-height) + 1);
}

#header.sticky-header:not(.static-sticky).sticky-style-2 #primary-menu,
#header.sticky-header:not(.static-sticky).sticky-style-3 #primary-menu {
    border-top: 0;
}

#header.sticky-header:not(.static-sticky) #top-search,
#header.sticky-header:not(.static-sticky) #top-cart,
#header.sticky-header:not(.static-sticky) #top-lang,
#header.sticky-header:not(.static-sticky) #side-panel-trigger {
    margin-top: $sticky-menu-padding + 1 !important;
    margin-bottom: $sticky-menu-padding + 1 !important;
}

#header.sticky-header:not(.static-sticky) #top-lang .top-lang-content,
#header.sticky-header:not(.static-sticky) #top-cart .top-cart-content {
    top: 40px;
}

#header.sticky-header.sticky-style-2 #primary-menu {
    border-top: 0;
}


/* Primary Menu - Style 3
-----------------------------------------------------------------*/

#header.sticky-header:not(.static-sticky) #primary-menu.style-3 > ul > li > a {
    margin: 0;
    border-radius: 0;
}


/* Primary Menu - Style 4
-----------------------------------------------------------------*/

#header.sticky-header:not(.static-sticky) #primary-menu.style-4 > ul > li > a {
    padding-top: 8px;
    padding-bottom: 8px;
    margin: 10px 0;
}


/* Primary Menu - Style 5
-----------------------------------------------------------------*/

#header.sticky-header:not(.static-sticky) #primary-menu.style-5 > ul > li {
    margin-left: 2px;
}

#header.sticky-header:not(.static-sticky) #primary-menu.style-5 > ul > li:first-child {
    margin-left: 0;
}

#header.sticky-header:not(.static-sticky) #primary-menu.style-5 > ul > li > a {
    line-height: 22px;
    padding-top: 19px;
    padding-bottom: 19px;
}

#header.sticky-header:not(.static-sticky) #primary-menu.style-5 > ul > li > a > div {
    padding: 0;
}

#header.sticky-header:not(.static-sticky) #primary-menu.style-5 > ul > li > a i {
    display: inline-block;
    width: 16px;
    top: -1px;
    margin: 0 6px 0 0;
    font-size: 14px;
    line-height: 22px;
}


/* Primary Menu - Sub Title
-----------------------------------------------------------------*/

#header.sticky-header:not(.static-sticky) #primary-menu.sub-title > ul > li,
#header.sticky-header:not(.static-sticky) #primary-menu.sub-title.style-2 > div > ul > li {
    background: none !important;
    margin-left: 2px;
}

#header.sticky-header:not(.static-sticky) #primary-menu.sub-title > ul > li:first-child,
#header.sticky-header:not(.static-sticky) #primary-menu.sub-title.style-2 > div > ul > li:first-child {
    margin-left: 0;
}

#header.sticky-header:not(.static-sticky) #primary-menu.sub-title > ul > li > a,
#header.sticky-header:not(.static-sticky) #primary-menu.sub-title > div > ul > li > a {
    line-height: 22px;
    padding: 19px 15px;
    text-transform: none;
    font-size: 14px;
    border-top: 0;
}

#header.sticky-header:not(.static-sticky) #primary-menu.sub-title ul li span {
    display: none;
}


/* ----------------------------------------------------------------
    Page Menu
-----------------------------------------------------------------*/

#page-menu,
#page-menu-wrap {
    position: relative;
    height: 44px;
    line-height: 44px;
    text-shadow: 1px 1px 1px rgba(black, 0.1);
}

#page-menu-wrap {
    z-index: 99;
    background-color: $theme-color;
    transform: translate3d(black);
    -webkit-backface-visibility: hidden;
}

#page-menu.sticky-page-menu #page-menu-wrap {
    position: fixed;
    z-index: 99;
    left: 0;
    width: 100%;
    box-shadow: 0 0 15px rgba(black, 0.3);
}

#header.static-sticky ~ #page-menu.sticky-page-menu #page-menu-wrap,
#header.static-sticky ~ #content #page-menu.sticky-page-menu #page-menu-wrap {
    top: 100px;
}

#header.no-sticky ~ #page-menu.sticky-page-menu #page-menu-wrap,
#header ~ #content #page-menu.sticky-page-menu #page-menu-wrap,
#header.no-sticky ~ #content #page-menu.sticky-page-menu #page-menu-wrap {
    top: 0;
}

#header.sticky-header ~ #page-menu.sticky-page-menu #page-menu-wrap,
#header.sticky-header ~ #content #page-menu.sticky-page-menu #page-menu-wrap {
    top: 60px;
}

#page-submenu-trigger {
    top: 0;
    margin-top: 0;
    left: auto;
    right: 15px;
    width: 40px;
    height: 44px;
    line-height: 44px;
}

#page-menu .menu-title {
    float: left;
    color: #FFF;
    font-size: 20px;
    font-weight: 300;
}

#page-menu .menu-title span {
    font-weight: 600;
}

#page-menu nav {
    position: relative;
    float: right;
}

#page-menu nav ul {
    margin-bottom: 0;
    height: 44px;
}

#page-menu nav li {
    position: relative;
    float: left;
    list-style: none;
}

#page-menu nav li a {
    display: block;
    height: 28px;
    line-height: 28px;
    margin: 8px 4px;
    padding: 0 14px;
    color: #FFF;
    font-size: 14px;
    border-radius: 14px;
}

#page-menu nav li a .icon-angle-down {
    margin-left: 5px;
}

body:not(.device-touch) #page-menu nav li a {
    transition: all 0.2s ease-in-out;
}

#page-menu nav li:hover a,
#page-menu nav li.current a {
    background-color: rgba(black, 0.15);
}

#page-menu li:hover ul {
    display: block;
}

#page-menu ul ul {
    display: none;
    position: absolute;
    width: 150px;
    top: 44px;
    left: 0;
    z-index: 149;
    height: auto;
    background-color: $theme-color;
}

#page-menu ul ul ul {
    display: none !important;
}


/* Disable Multi-Level Links */

#page-menu ul ul li {
    float: none;
}

#page-menu ul ul a {
    height: auto;
    line-height: 22px;
    margin: 0;
    padding: 8px 14px;
    border-radius: 0;
    background-color: rgba(black, 0.15);
}

#page-menu ul ul li:hover a,
#page-menu ul ul li.current a {
    background-color: rgba(black, 0.3);
}


/* Page Menu - Dots Style
-----------------------------------------------------------------*/

@media (min-width: 992px) {
    #page-menu.dots-menu,
    #page-menu.dots-menu #page-menu-wrap {
        position: fixed;
        z-index: 99;
        top: 50%;
        left: auto;
        right: 20px;
        width: 24px;
        height: auto;
        line-height: 1.5;
        background-color: transparent;
        text-shadow: none;
    }
    #page-menu.dots-menu #page-menu-wrap {
        position: relative;
        box-shadow: none;
    }
    #page-menu.dots-menu .menu-title {
        display: none;
    }
    #page-menu.dots-menu nav li div {
        float: none;
        width: 24px;
    }
    #page-menu.dots-menu nav ul {
        height: auto;
    }
    #page-menu.dots-menu nav ul li {
        float: none;
        width: 14px;
        height: 14px;
        margin: 10px 6px;
    }
    #page-menu.dots-menu nav li a {
        position: relative;
        width: 10px;
        height: 10px;
        line-height: 1.5;
        padding: 0;
        margin: 0 2px;
        background-color: rgba(black, 0.4);
        border-radius: 50%;
        color: #777;
    }
    #page-menu.dots-menu nav li a:hover {
        background-color: rgba(black, 0.7);
    }
    #page-menu.dots-menu nav li.current a {
        background-color: $theme-color;
    }
    #page-menu.dots-menu nav li div {
        position: absolute;
        width: auto;
        top: -11px;
        right: 25px;
        background-color: $theme-color;
        color: #FFF;
        padding: 5px 14px;
        white-space: nowrap;
        pointer-events: none;
        text-shadow: 1px 1px 1px rgba(black, 0.15);
        border-radius: 2px;
        opacity: 0;
    }
    body:not(.device-touch) #page-menu.dots-menu nav li div {
        transition: all 0.2s ease-in-out;
    }
    #page-menu.dots-menu nav li div:after {
        position: absolute;
        content: '';
        top: 50%;
        margin-top: -6px;
        left: auto;
        right: -5px;
        width: 0;
        height: 0;
        border-top: 7px solid transparent;
        border-bottom: 7px solid transparent;
        border-left: 6px solid $theme-color;
    }
    #page-menu.dots-menu nav li a:hover div {
        opacity: 1;
        right: 30px;
    }
}


/* ----------------------------------------------------------------
    One Page
-----------------------------------------------------------------*/

.one-page-arrow {
    position: absolute;
    z-index: 3;
    bottom: 20px;
    left: 50%;
    margin-left: -16px;
    width: 32px;
    height: 32px;
    font-size: 32px;
    text-align: center;
    color: #222;
}


